.header {
    margin-top: 60px;

}

.headerTitles {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Lora', serif;
    color: #444;
}

.headerTitleSm {
    position: absolute;
    top: 15%;
    font-size: 20px;
}

.headerTitleLg {
    position: absolute;
    top: 18%;
    font-size: 80px;
    color: aqua;
    z-index: 999;
}

.headerImg {
    width: 100%;
    height: 450px;
    margin-top: 80px;
    object-fit: cover;
    z-index: -1;

}

